@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap');

.App {
  text-align: center;
  padding: 0;
  margin: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
  background: transparent;
  z-index: 1;
  transition: all .4s;
  height: 90px;

}

.headerActive {
  background: white;
  box-shadow: 1px 2px 10px 3px rgba(0, 0, 0, 0.2);
  height: 70px;
  z-index: 1;
  transition: all .4s;
}

.link {
  font-weight: 500;
  text-decoration: none;
  color: #2e2e2e;
  transition: .2s color;
  padding: 10px;
}

.link:hover {
  color: #5F9300;
  transition: .2s color;
}

.link::after {
  background-color: blue;

}

.link::before {
  color: #5F9300;
  background-color: red;

}

.arrowDown {
  width: 30px;
  height: 30px;
  transition: all .2s;
  color: #2e2e2e;
}

@media(min-width: 768px) {
  .arrowDown{
    width: 40px;
    height: 40px;
  }
}

.arrowDown:hover {
  color: #424242;
  transform: scale(1.1);
  transition: all .2s;
}

.cliente:hover .fotoCliente {
  transform: translate(1000px,0);
  -webkit-transform: translate(1000px,0); /** Chrome & Safari **/
  -o-transform: translate(1000px,0); /** Opera **/
  -moz-transform: translate(1000px,0); /** Firefox **/
  transition: all 1s;
}

.icono {
  width: 20px;
  height: 20px;
  color: white;
  transition: all .2s;
  
}

@media (max-width: 800px) {
  .icono {
    width: 15px;
    height: 15px;
  }
}

.icono:active {
  transform: scale(.9);
  transition: all .2s;
}

.input {
  font-family: 'Roboto', sans-serif;
}

.iconoContacto {
  margin-right: 5px;
}
